              p Sin importar el origen del pigmento, su composicin, incluso si era venenoso o no, lo relevante era <b><span class='txt-1'>diferenciarse de los dems</span></b> expresndose y plasmando su identidad en rocas, paredes, lienzos, vestuario, rostro, accesorios y dems objetos y elementos de uso cotidiano que permitan <b><span class='txt-1'>distinguir cada cultura</span></b> desde pocas primitivas.
              p El uso del color fue catalogado como <b><span class='txt-1'>esotrico y prohibido en la poca del oscurantismo,</span></b> pero ya en la edad moderna con el renacimiento resurgen las artes y su dinmica trayendo consigo la aparicin de los <b><span class='txt-1'>grandes artistas</span></b> del mundo civilizado. 

