              p.mx-5 Es fundamental pensar en todo lo que se puede lograr con el color; en primer lugar, <b><span class='txt-1'>es el rey de las emociones:</span></b> alegra, entristece, deprime, enferma, puede generar efectos de acercamiento o repudio, suaviza y enternece, pero tambin puede llegar a agredir; desde la<b><span class='txt-1'> psicologa del color</span></b> se plantea que puede alterar a las personas, incluso ser utilizado como un arma letal que afecta el comportamiento, puede apaciguar, tranquilizar e incluso reposar a las personas. 

