          .col-lg-6.mb-5.l-d2.py-4
            p Los tonos <b><span class='txt-1'> clidos</span></b> son mezclas que aparecen en un lado del crculo cromtico <b><span class='txt-1'>entre el rojo y el amarillo,</span></b> y, los tonos <b><span class='txt-1'>fros</span></b> los que estn en el lado opuesto, <b><span class='txt-1'>entre el azul y el violeta.</span></b> Se clasifican de esta forma por la cantidad de negro o blanco que contengan, dando paso a la vibracin del color, o sea el brillo y la opacidad que los caracteriza, desplegando as un sinnmero de tonalidades.
            p Los colores se definen de acuerdo a caractersticas, normas o reglas que se generan a partir de tcnicas aplicadas que permiten obtener<b><span class='txt-1'> nuevas propuestas de color,</span></b> partiendo de elementos como la tonalidad, la saturacin, el brillo, el contraste, la adyacencia, el complemento y la oposicin. La habilidad se adquiere desde el momento en que se empieza a jugar con el color en el <b><span class='txt-1'>crculo cromtico</span></b> de diversas maneras, dando como resultado cartas de color con caractersticas de sensibilidad, de acuerdo a conceptos o temas de inspiracin.

