    .row.justify-content-around.align-items-center
      .col-lg-10.align-self-center
        .row.justify-content-around.align-items-center.p-5.fnd-3
          p El conocimiento de la historia del crculo cromtico, permite entender la aplicacin que ha tenido en todas las artes visuales, jugando un papel protagnico como herramienta que transforma las estticas visuales en todos los espacios, lugares, momentos y en la apariencia esttica de los individuos, generando diferentes propuestas armnicas, de acuerdo a una sensibilidad expresada a partir del color.
          .col-md-8
            p El crculo cromtico es una<b><span class='txt-1'> representacin grfica que segmenta y organiza los colores</span></b>  con base en el tono o matiz de cada uno (Parra, 2013), es una descomposicin de la luz con respecto al espectro solar, sirve para segmentar los colores y se pueden clasificar como familias de las que se desprenden otros colores, a partir de las variaciones de intensidad de los tonos. 
          .col-md-4.p-4
            figure
              img.img-190.img-c(src='@/assets/tema1/img-17.svg')
          p.mb-5 La principal utilidad del crculo cromtico es que <b><span class='txt-1'>refleja el vnculo de armona y contraste entre los colores primarios con los colores secundarios y terciarios,</span></b> este concepto es valorado como una herramienta para descubrir y aplicar todas las combinaciones posibles de colores. <b><span class='txt-1'>En el mundo de la moda, es fundamental conocer la tcnica de transformacin que surge a partir del color con respecto a la apariencia esttica de las personas, reflejado y modificado en cada una de las colecciones.</span></b> El crculo cromtico permite generar diferentes clases de armonas de colores, que aplicados producen <b><span class='txt-1'>nuevas estticas</span></b> de transformacin visual, para lograr que en el mercado de la moda se generen diferentes opciones para cautivar los gustos y necesidades del usuario.

          .row.justify-content-around.align-items-center
            .col-lg-10.align-self-center
              .row.justify-content-around.align-items-center
