    #t_1_4.titulo-segundo
      h2 1.4 Escalas de color: armonas de color, contrastes, definicin y obtencin de volumen 
    p <b><span class='txt-1'>Armona es la habilidad de llevar un color a otro nivel superior o inferior,</span></b>  la escala se obtiene tomando los colores primarios y adicionando blanco o negro para obtener una gama ms grande de tonalidades, de hecho, toda la escala cromtica debe llegar al negro puro o tambin al blanco transparente en cada uno de sus matices (Blanco verdoso, blanco azulado, blanco rojizo, funcionando para cualquier color), de igual manera se puede dirigir hacia el negro.

    TabsA.color-secundario.mb-5

      .tarjeta.color-primario--borde.p-4(titulo="La armona")
        .row
          .col-xl-8
            h4 La armona
            p La armona, consiste en el<b><span class='txt-1'> control y equilibrio</span></b>  de la sensacin que produce la tonalidad, pasando de una escala a otra con la sutileza suficiente para que la percepcin no se vea afectada por saltos fuertes de color en el valor cromtico; se evidencia en las cantidades de pigmento mezclados para la obtencin de otros colores en cada nivel del crculo cromtico.
          .col-xl-4
            figure
              img.img-c.img-250(src='@/assets/tema1/img-19.svg')
        
      .tarjeta.color-primario--borde.p-4.aaa(titulo="El contraste")
        .row
          .col-xl-8
            h4 El contraste
            p <b><span class='txt-1'>El contraste se genera a partir de la eleccin de dos colores en igual intensidad, pero enfrentados, para ello, es importante que se encuentren en el mismo nivel en el crculo cromtico.</span></b>  Una de las caractersticas que se produce, es que se divide el espacio, la estructura y en el caso del vestuario, la silueta, separando de una forma radical la composicin entre colores.
          .col-xl-4
            figure
              img.img-c.img-250(src='@/assets/tema1/img-20.svg')

      .tarjeta.color-primario--borde.p-4(titulo="El volumen")
        .row
          .col-xl-8
            h4 El volumen
            p Para generar volumen es preciso aplicar la tcnica del degrad, que es cuando se parte de un color y se lleva hacia la sombra o la luz, teniendo en cuenta que son efectos pticos que se logran sin dejar de ser una ilusin, pero que pueden generar efectos de volumen, movilidad, realismo, temperatura, sensacin de pesado o liviandad, efectos de capas, pliegues, recogidos que es lo que realmente define las lneas del vestuario. 
            p Es importante agregar que el <b><span class='txt-1'>efecto 3D</span></b>  se logra de alguna forma a travs del manejo del color, produciendo una <b><span class='txt-1'>ilusin ptica.</span></b> 

          .col-xl-4
            figure
              img.img-c.img-250(src='@/assets/tema1/img-21.svg')

