      h2 1.6 Psicologa y simbologa del color: definicin y caractersticas

    .bloque-texto-g.color-acento-botones.p-3.p-sm-4.p-md-5.mb-5.aaa
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/tema1/img-23.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-4 La <b><span class='txt-1'>psicologa del color</span></b> es la que estudia en el mundo, lo sensorial, los sentimientos y transformaciones de los individuos con relacin al uso del color; se trata del <b><span class='txt-1'>significado que a travs de la historia se ha construido</span></b> en un sinnmero de referentes, ya sea desde lo social, cultural, actitudinal y comportamental. Cada individuo desde su propia experiencia lo aprueba o rechaza y esto tiene que ver directamente con lo que se conoce como el gusto de cada persona.
        br 
        br
        p.mb-0 <b><span class='txt-1'>La esttica</span></b> presenta un sinnmero de <b><span class='txt-1'>opciones </span></b>desde el <b><span class='txt-1'>manejo del color,</span></b> donde hacen su aparicin las tonalidades, generando sensaciones de alegra, tristeza y tranquilidad, que son fenmenos donde el color siempre est presente y se sienten de una manera tan evidente que, con la presencia del solo color, se puede leer o traducir en sentimientos.

    .row.mb-5
      .bloque-texto-e.color-primario.p-4.fnd-1.fnd-b 
        .bloque-texto-e__text
