          p #[span(style="margin-left:6rem;")    Las personas se han hecho cada vez ms conscientes de la importancia de los colores, en parte por la gran cantidad de analistas de color que se han empleado en el mercado actual. Estos profesionales han llamado la atencin de los consumidores sobre los colores que tienen significados simblicos y provocan sensaciones individuales.]
          p #[span(style="margin-left:5px;")  Hay que tener en cuenta que la primera impresin es la ms importante y que el color del vestuario enva mensajes no verbales definitivos sobre lo que el individuo quiere comunicar.] 
            i.fas.fa-quote-right.txt-1

    p El color es un elemento enamorador de una prenda y/o coleccin, por esto lo ms importante es la interpretacin que se le d al concepto, de donde se definen los colores de la coleccin.

    p La naturaleza, es la diosa en generar armonas cromticas perfectamente elaboradas con una maravillosa belleza y complejidad, que conectan de una manera exquisita con el entorno. 

