              .h5.mb-0 (Decker, 2017).
          p.mb-5 Para entender el tema, es muy apropiado imaginarse Qu sera de la moda si no existiera el color!, se podra decir que el mundo sera gris, opaco, apacible, casi que la tristeza reinara; todo lo contrario, ocurre cuando hablamos del color, que se asemeja a la vida misma, se tiene todo por vivir, conocer, alcanzar y ser feliz; es cuando aparece una gama de colores fuertes y medios llamados colores clidos. 
