            p Es tan importante<b><span class='txt-1'> el color para la moda</span></b> que llega a transformar, <b><span class='txt-1'>favorecer o perjudicar</span></b> a la persona que lo porta: una buena seleccin de color puede hacer lucir a la persona ms joven, alegre, con la piel ms saludable, que sus ojos brillen y el cabello luzca bien. Existen dos tipos de piel, clida y fra, cada una se puede ver beneficiada o perjudicada dependiendo del tipo de color que acompae y cubra su cuerpo, los colores que lleva la persona influyen directamente en cmo se siente.
            p Cada individuo tiene su propia percepcin del color, depende de la experiencia que haya tenido con este; los gustos por los colores, tonalidades o efectos de los colores varan debido a que el reflejo de la luz y la visin hace que sea muy personal, teniendo un comn denominador llamado temporada y color de moda.
